<template>
  <div class="form-check col">
    <input
      v-if="checked"
      class="form-check-input"
      type="checkbox"
      :name="this.name"
      :id="this.id"
      :value="value"
      checked
      v-model="isChecked"
      @change="formChecked"
    />
    <input
      v-else
      class="form-check-input"
      type="checkbox"
      :name="this.name"
      :id="this.id"
      :value="value"
      v-model="isChecked"
      @change="formChecked"
    />
    <label class="form-check-label" for="flexCheckDefault">
      {{ this.label }}
    </label>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isChecked: false,
    };
  },
  computed: {
    id: function () {
      return this.name.replace('_', '-');
    },
  },
  props: {
    checked: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    value: {
      type: String,
      default: '',
    },
  },
  methods: {
    formChecked() {
      this.$emit('is-check', this.isChecked);
    },
  },
};
</script>

<style></style>
